import {
    MESSAGE, HIDEMESSAGE
  } from '../actions/constants';

  
  const INITIAL_STATE = {
    errorMsg: "",
    errorType: "",
  };
  
  function newState(state, errorMsg, errorType ) {
    return { ...state,  errorMsg, errorType };
  }
  
  export default function (state = INITIAL_STATE, action) {
    const isError = /.*_REJECTED(.*?)$/;
    switch (action.type) {
      case MESSAGE:
        return newState(state, action.text.errorMsg, action.text.errorType);
      case HIDEMESSAGE:
        return INITIAL_STATE;
      default:
        if (action.type.match(isError)) {
          if (action.payload  && action.payload.isAxiosError === true) {
            return newState(state, action.payload.message, "error");
          }
        }        
        return INITIAL_STATE;
    }
  }
  