import {
    LOGIN,
    LOGOUT,
  } from '../actions/constants';
  
  const INITIAL_STATE = {
    isLoggedIn: false,
    info: {
      email: '',
      firstName: '',
      lastName: '',
      role: '',
    },
  };
  
  function newState(state, info, isLoggedIn) {
    return { ...state, info, isLoggedIn };
  }
  
  export default function (state = INITIAL_STATE, action) {
    const isError = /.*_ERROR(.*?)$/;
    switch (action.type) {
      case `${LOGIN}_FULFILLED`:
        localStorage.setItem('access_token', action.payload.data.accessToken);
        return newState(state, action.payload.data.user, true);
      case `${LOGIN}_REJECT`:
        return INITIAL_STATE;
      case `${LOGOUT}_FULFILLED`:
        return INITIAL_STATE;
      default:
        if (action.type.match(isError)) {
          if (action.payload.status === 401) {
            return INITIAL_STATE;
          }
        }
        return state;
    }
  }
  