import React from 'react';
import { Route, Switch } from 'react-router';
import RouteWithLayout from './components/RouteWithLayout';
import { Main as MainLayout, Minimal as MinimalLayout } from './layouts';

//import PageContainer from './pages/PageContainer';
import TestPage from './pages/TestPage';
import Dashboard from './pages/Dashboard';
import { LoginPage } from './pages/auth';
import Home from './pages/Home';
import PageNotFound from './pages/PageNotFound';

export default (
  <Route path="/">
      <Switch>
        <RouteWithLayout exact path="/" layout={MainLayout} component={Home}/>
        <RouteWithLayout path="/dashboard" layout={MainLayout} component={Dashboard}/>
        <RouteWithLayout path="/home" layout={MainLayout} component={Home}/>
        <RouteWithLayout path="/login" layout={MinimalLayout} component={LoginPage} public={true}/>
        <Route path="/testPage" component={TestPage} />
        <Route path="*" component={PageNotFound} />
      </Switch>
  </Route>
);
